(function() {
    'use strict';

    angular
        .module('app')
        .directive('playerInitPlaylist', ['$timeout', playerInitPlaylist]);

    function playerInitPlaylist($timeout){
    	var directive = {
            restrict: 'A',
            link: link,
        };
        return directive;

        function link(scope, element, attrs) {
        	$timeout(function(){
        		element.triggerHandler('click');
        	})
        }
    }
})();
