(function() {
    'use strict';

    angular
        .module('app')
        .directive('replace', [replace]);

    function replace(){
    	var directive = {
            restrict: 'A',
            replace: true,
            templateUrl: templateUrl,
        };
        return directive;

        function templateUrl(element, attrs){
            return attrs.replace;
        }
    }
})();
(function() {
	'use strict';

	angular
		.module('app')
		.run(runBlock)
		.config(['$locationProvider', html5Config])

	/** @ngInject */
	function runBlock($log) {
		$log.debug('runBlock end');
	}

	function html5Config($locationProvider) {
		$locationProvider.html5Mode(true);
	}

})();

